.cover-page {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  height: calc(100vh - 280px);
}

.cover-title {
  text-transform: uppercase;
  font-weight: bold;
  font-size: 2.2rem;
}

.cover-andress {
  text-transform: uppercase;
}

.cover-functions {
  font-weight: bold;
}

.lista-funcoes-nomes {
  list-style: none;
  list-style-position: inside;
}

.lista-funcoes-nomes li {
  margin-left: -32px;
}

.article-container{
  text-align: justify;
  padding: 0 20px;
  margin-bottom: 30px;
}

.article-title {
  text-transform: uppercase;
  font-size: 1.2rem;
  font-weight: bolder;
}

.article-paragraph {
  text-indent: 20px;
  margin: 0;
}

.article-table tbody tr td:nth-child(1) {
  padding: 5px 20px;
  width: 200px;
}

.assinaturas-container {
  display: flex;
  justify-content: center;
}

.assinaturas-container p {
  margin: 0;
}

.assinaturas-container p:nth-child(1) {
  margin-bottom: 10px;
}

.campo-assinatura {
  padding: 0 30px;
}

.campo-assinatura p:nth-child(1) {
  margin-bottom: 20px;
}

.footer-space {
  height: 115px;
}

.btn-warning {
  margin: 5px;
}

.tabela-exposicao {
  text-align: center;
}

.tabela-exposicao th, .tabela-exposicao td{
  border: 1px solid black;
  padding: 5px;
}

.memoria-calculo {
  margin-top: 10px;
  text-align: justify;
  display: flex;
  flex-wrap: wrap;
  width: 120%;
}

@media print {
  .btn-warning {
    visibility: hidden;
  }
}